.searchBox {
    position: absolute;
    top: -3%;
    left: 50%;
    transform:  translate(-50%,50%);
    background: linear-gradient(to right , #cb2d3e , #ef473a);
    height: 30px;
    border-radius: 40px;
    padding: 10px;
    display: inherit;
}

.searchBox:hover > .searchInput {
    width: 150px;
    padding: 0 3px;
}

.searchBox:hover > .searchButton {
  background: white;
  color : #2f3640;
}

.searchButton {
    color: #13f75b;
    float: right;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #0c31f1;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
    outline: none;
    cursor: pointer;
}

.searchInput {
    border:none;
    background: none;
    outline:none;
    float:left;
    padding: 0;
    color: white;
    font-size: 16px;
    transition: 0.4s;
    line-height: 40px;
    width: 0px;

}

